/* Support Section */
.support {
  padding: 100px 0;
  background: var(--bg-secondary);
  position: relative;
  overflow: hidden;
  clip-path: inset(0);
}

/* Make background transparent in light mode to show video */
:root[data-theme="light"] .support {
  background: transparent;
}

/* Video Background */
.support .video-background {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 0;
  overflow: hidden;
}

.support .video-background video {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  object-fit: cover;
  filter: blur(3px);
}

/* Theme video transitions */
.support .theme-video {
  opacity: 0;
  transition: opacity 0.8s ease-in-out;
}

.support .theme-video.active {
  opacity: 1;
}

.support .video-background .video-overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.85);
}

/* Lighter overlay for light mode */
:root[data-theme="light"] .support .video-background .video-overlay {
  background: rgba(255, 255, 255, 0.8);
}

.support .container {
  position: relative;
  z-index: 1;
}

/* Mobile video fix */
@media (max-width: 768px) {
  .support .video-background video {
    width: auto;
    height: 100%;
    min-width: 100%;
  }
}

.support::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 1px;
  background: linear-gradient(90deg, transparent, var(--accent-primary), transparent);
}

.support-header {
  text-align: center;
  margin-bottom: 60px;
}

/* Support Options Grid */
.support-options {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 30px;
  max-width: 1000px;
  margin: 0 auto 50px;
}

/* Support Card */
.support-card {
  background: rgba(255, 255, 255, 0.1);
  backdrop-filter: blur(10px);
  -webkit-backdrop-filter: blur(10px);
  border-radius: 20px;
  padding: 40px 30px;
  text-align: center;
  text-decoration: none;
  color: var(--text-primary);
  border: 1px solid rgba(255, 255, 255, 0.2);
  transition: all 0.4s cubic-bezier(0.4, 0, 0.2, 1);
  position: relative;
  overflow: hidden;
}

.support-card::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 4px;
  background: var(--card-color);
  transform: scaleX(0);
  transition: transform 0.4s ease;
}

.support-card:hover {
  transform: translateY(-10px);
  box-shadow: 0 20px 40px rgba(0, 0, 0, 0.15);
  border-color: var(--card-color);
}

.support-card:hover::before {
  transform: scaleX(1);
}

/* Support Icon */
.support-icon {
  width: 80px;
  height: 80px;
  border-radius: 50%;
  background: rgba(255, 255, 255, 0.15);
  backdrop-filter: blur(5px);
  -webkit-backdrop-filter: blur(5px);
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 0 auto 20px;
  transition: all 0.4s ease;
}

.support-card:hover .support-icon {
  transform: scale(1.1);
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.1);
}

/* Support Name */
.support-name {
  font-size: 1.4rem;
  font-weight: 700;
  margin-bottom: 10px;
  color: #ffffff;
}

/* Support Description */
.support-description {
  font-size: 0.95rem;
  color: rgba(255, 255, 255, 0.8);
  line-height: 1.6;
  margin-bottom: 20px;
}

/* Support Button */
.support-btn {
  display: inline-flex;
  align-items: center;
  gap: 8px;
  padding: 12px 24px;
  background: rgba(255, 255, 255, 0.15);
  color: var(--card-color);
  border-radius: 30px;
  font-weight: 600;
  font-size: 0.9rem;
  transition: all 0.3s ease;
}

.support-btn svg {
  transition: transform 0.3s ease;
}

.support-card:hover .support-btn {
  background: var(--card-color);
  color: white;
}

.support-card:hover .support-btn svg {
  transform: translateX(5px);
}

/* Support Message */
.support-message {
  text-align: center;
  padding: 30px 40px;
  background: linear-gradient(135deg, var(--accent-primary), var(--accent-secondary));
  border-radius: 20px;
  max-width: 600px;
  margin: 0 auto;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 15px;
  flex-wrap: wrap;
}

.support-message .heart-icon {
  width: 50px;
  height: 50px;
  background: rgba(255, 255, 255, 0.2);
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  color: white;
  transition: transform 0.3s, color 0.3s;
}

@keyframes heartbeat {
  0%, 100% {
    transform: scale(1);
  }
  50% {
    transform: scale(1.1);
  }
}

.support-message p {
  color: white;
  font-size: 1.1rem;
  font-weight: 500;
  margin: 0;
  flex: 1;
  min-width: 200px;
}

/* Responsive Design */
@media (max-width: 768px) {
  .support {
    padding: 60px 0;
  }

  .support .section-title {
    font-size: 1.75rem;
  }

  .support .section-subtitle {
    font-size: 0.95rem;
    padding: 0 10px;
  }

  .support-header {
    margin-bottom: 40px;
  }

  .support-options {
    grid-template-columns: 1fr;
    gap: 16px;
    padding: 0 15px;
  }

  .support-card {
    padding: 28px 22px;
  }

  .support-icon {
    width: 60px;
    height: 60px;
  }

  .support-icon svg {
    width: 26px;
    height: 26px;
  }

  .support-name {
    font-size: 1.15rem;
  }

  .support-description {
    font-size: 0.9rem;
  }

  .support-btn {
    padding: 10px 20px;
    font-size: 0.85rem;
  }

  .support-message {
    flex-direction: column;
    text-align: center;
    padding: 20px 16px;
    margin: 0 15px;
    border-radius: 16px;
  }

  .support-message p {
    font-size: 0.95rem;
  }

  .heart-icon {
    margin-bottom: 10px;
    transition: transform 0.3s, color 0.3s;
  }
  .heart-icon svg {
    transition: color 0.3s;
  }
  .heart-icon.heart-clicked svg {
    color: #e53935 !important;
    filter: drop-shadow(0 0 8px #e53935);
  }
  .heart-icon.heart-pop {
    animation: heart-pop 0.9s cubic-bezier(.68,-0.55,.27,1.55);
  }
  @keyframes heart-pop {
    0% { transform: scale(1); }
    20% { transform: scale(1.3); }
    40% { transform: scale(0.95); }
    60% { transform: scale(1.15); }
    80% { transform: scale(1); }
    100% { transform: scale(1); }
  }
  }
  @keyframes heart-pop {
    0% { transform: scale(1); }
    20% { transform: scale(1.3); }
    40% { transform: scale(0.95); }
    60% { transform: scale(1.15); }
    80% { transform: scale(1); }
    100% { transform: scale(1); }
  }


@media (max-width: 480px) {
  .support {
    padding: 50px 0;
  }

  .support .section-title {
    font-size: 1.5rem;
  }

  .support-header {
    margin-bottom: 30px;
  }

  .support-card {
    padding: 22px 18px;
  }

  .support-icon {
    width: 54px;
    height: 54px;
    margin-bottom: 14px;
  }

  .support-name {
    font-size: 1.1rem;
  }

  .support-btn {
    padding: 10px 18px;
    font-size: 0.8rem;
  }

  .support-message {
    padding: 18px 14px;
  }
}

/* QR Code Modal */
.qr-modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.8);
  backdrop-filter: blur(5px);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1000;
  padding: 20px;
  animation: fadeIn 0.3s ease;
}

@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

.qr-modal {
  background: var(--bg-primary);
  border-radius: 24px;
  padding: 40px;
  max-width: 400px;
  width: 100%;
  text-align: center;
  position: relative;
  box-shadow: 0 25px 50px rgba(0, 0, 0, 0.3);
  animation: slideUp 0.3s ease;
}

@keyframes slideUp {
  from {
    opacity: 0;
    transform: translateY(30px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.qr-modal-close {
  position: absolute;
  top: 15px;
  right: 15px;
  background: var(--bg-secondary);
  border: none;
  border-radius: 50%;
  width: 40px;
  height: 40px;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  color: var(--text-secondary);
  transition: all 0.3s ease;
}

.qr-modal-close:hover {
  background: var(--accent-primary);
  color: white;
  transform: rotate(90deg);
}

.qr-modal-title {
  font-size: 1.5rem;
  font-weight: 700;
  margin-bottom: 25px;
}

.qr-modal-image {
  background: white;
  border-radius: 16px;
  padding: 20px;
  margin-bottom: 20px;
  display: inline-block;
}

.qr-modal-image img {
  width: 250px;
  height: 250px;
  object-fit: contain;
  display: block;
}

.qr-modal-description {
  color: var(--text-secondary);
  font-size: 0.95rem;
}

/* Button style for QR cards */
button.support-card {
  cursor: pointer;
  border: 1px solid var(--border-color);
  font-family: inherit;
}

@media (max-width: 480px) {
  .qr-modal {
    padding: 30px 20px;
  }

  .qr-modal-image img {
    width: 200px;
    height: 200px;
  }

  .qr-modal-title {
    font-size: 1.3rem;
  }
}

/* Light mode text adjustments for better visibility */
:root[data-theme="light"] .support .section-label {
  color: #1a1a2e;
}

:root[data-theme="light"] .support .section-title {
  color: #1a1a2e;
}

:root[data-theme="light"] .support .section-subtitle {
  color: #64748b;
}

:root[data-theme="light"] .support .support-name {
  color: #1a1a2e;
}

:root[data-theme="light"] .support .support-description {
  color: #64748b;
}

:root[data-theme="light"] .support .support-btn {
  color: #1a1a2e;
}

:root[data-theme="light"] .support .qr-modal-title {
  color: #1a1a2e;
}

:root[data-theme="light"] .support .qr-modal-description {
  color: #64748b;
}

:root[data-theme="light"] .support .support-message p {
  color: #1a1a2e;
}

:root[data-theme="light"] .support .support-card {
  background: rgba(255, 255, 255, 0.8);
  border: 1px solid rgba(0, 0, 0, 0.15);
}

:root[data-theme="light"] .support .qr-modal {
  background: rgba(255, 255, 255, 0.95);
  box-shadow: 0 25px 50px rgba(0, 0, 0, 0.2);
}
